.footercontainer {
  padding: 100px 50px 0px;
  background-color: #333;
}
.topfooter {
  display: flex;
  flex-basis: 100%;
  justify-content: space-between;
  padding-bottom: 40px;
}
.bottomfooter {
  display: flex;
  flex-basis: 100%;
  justify-content: space-between;
}
.section1,
.section2,
.section3,
.section4,
.section5,
.section6,
.section7 {
  color: white;
}
.section1 {
  flex-basis: 60%;
}
.section2,
.section3,
.section4 {
  flex-basis: 40%;
  padding-left: 20px;
}
.section5 {
  flex-basis: 50%;
}
.section6,
.section7 {
  flex-basis: 50%;
  padding-left: 20px;
}

.footercontainer p {
  font-size: 16px;
}
@media screen and (max-width: 978px) {
  .topfooter {
    flex-direction: column;
  }
  .bottomfooter {
    flex-direction: column;
  }
  .section1,
  .section2,
  .section3,
  .section4,
  .section5,
  .section6,
  .section7 {
    flex: 1;
    padding: 0;
  }
}
