.page1container {
  padding: 100px 50px 100px;
}
.page1main {
  display: flex;
}
.page1heading {
  padding-bottom: 20px;
  text-align: center;
}
.page1heading > span {
  font-size: 10px;
}
.page1heading .logo {
  font-size: calc(1.5vw + 1rem);
}
.page1image > img {
  height: auto;
  width: 100%;
}
.page1heading,
.page1image,
.imageinfo {
  flex: 1;
}

.imageinfobox {
  width: 90%;
}
.imageinfonav {
  display: flex;

  padding-bottom: 20px;
}
#dot {
  padding-left: 15px;
  padding-right: 15px;
  line-height: 1.5rem;
}
.imageinfonav div a {
  font-size: 14px;
}
.imageinfohead {
  font-size: calc(1vw + 1rem);
  padding-bottom: 20px;
}
/*button*/
.imageinfodesc > button {
  width: 100%;
  height: 60px;
  color: white;
  outline: none;
  border: hidden;
  cursor: pointer;
  background-color: #252525;
  font-size: 18px;
}
.imageinfodesc > button:hover {
  transition: all 0.3s ease-out;
  background-color: black;
}
/*footer*/
.page1footercontainer {
  padding: 80px 0;
}
.page1footer {
  text-align: center;
}
.page1footerhead {
  font-size: 16px;
  cursor: pointer;
  line-height: 2;
}
.page1footerhead:hover {
  text-decoration: underline;
}
.page1footerinfo {
  font-size: 16px;

  color: grey;
}
@media screen and (max-width: 1024px) {
  .page1container {
    padding: 20px;
  }
  .page1main {
    flex-direction: column;
  }
  .imageinfobox {
    width: 100%;
  }
}
