.page2container {
  display: flex;
  padding: 100px 50px 100px;
  background: rgba(0, 0, 0, 0.1);
}
.page2vidcontainer {
  flex-basis: 50%;
  position: relative;
}
.page2infocontainer {
  flex-basis: 50%;
  padding: 100px;
}
.page2info {
  width: 100%;
}
.page2vidcontainer > video {
  width: 100%;
  height: auto;
  object-fit: fill;
  background: cover no-repeat;
  background-position: center;
  outline: none;
}
@media screen and (max-width: 1439px) {
  .page2container {
    flex-direction: column;
    padding: 0;
  }
}
