.navmaincontainer {
  position: sticky;
  top: 0;
  z-index: 1;
  width: 100%;
}
.abovenav {
  background-color: #252525;
  color: white;
  text-align: center;
  padding: 15px 0px 15px;
  font-size: calc(0.5vw + 12px);
  cursor: pointer;
}
.abovenav:hover {
  text-decoration: underline;
}

.navbarcontainer {
  height: 80px;
  position: relative;
  background-color: white;
  z-index: 1;
  display: flex;
  align-items: center;
}
.navbarcontainer nav {
  display: flex;

  flex: 1;
}
.leftnav {
  display: flex;
  justify-content: flex-start;
  padding-left: 20px;
  flex: 70%;
}
.leftnav > div {
  padding-left: 20px;
}
.rightnav {
  display: flex;
  flex: 30%;
  justify-content: flex-end;

  padding-right: 50px;
}
.rightnav > div {
  padding-right: 20px;
}
a {
  text-decoration: none;
  position: relative;
  color: #252525;
  font-size: calc(0.4vw + 12px);
}
a::before {
  content: "";
  position: absolute;
  width: 0;
  height: 1px;
  bottom: 0;

  background-color: #252525;
  visibility: hidden;
  transition: all 0.3s ease-out;
}
a:hover::before {
  visibility: visible;
  width: 100%;
}
.bar {
  display: none;
  position: absolute;
  cursor: pointer;
}
@media screen and (max-width: 767px) {
  .navbarcontainer nav {
    position: absolute;
    flex-direction: column;
    top: 80px;
    width: 100%;
    height: 300px;
    background-color: white;
    padding-top: 40px;
    transition: all 0.3s ease-out;
  }
  .bar {
    display: block;
    right: 5%;
    top: 50%;
    transform: translate(-5%, -50%);
  }
  .leftnav,
  .rightnav {
    flex-direction: column;
    padding: 0;
    flex: 0;
    width: 100vw;
    text-align: center;
  }
  .leftnav div a,
  .rightnav div a {
    font-size: 1rem;
  }
  .rightnav > div {
    padding-right: 0px;
    height: 40px;
  }

  .leftnav > div {
    padding-left: 0px;
    height: 40px;
  }
}
